@import 'theme.scss';

.publication-card {
    padding: 12px;

    &__image {
        margin-top: 10px;
        width: 70%; }

    a {
        margin-right: 10px;
        text-decoration: underline;
        @include themify($themes) {
            text-decoration-color: themed('accent-color'); } } }

@media (max-width: 800px) {
    .publication-card {
        &__image {
            width: 100%; } } }
